    font-size: 25px;
    padding: 10px;
  }

  .add_restaurant_button:hover {
    background-color: #d94148;
  }
}

@media screen and (max-width: 1199px) {
  .navigation {
    margin: auto;
    background-color: #cb202d;
    padding-right:20px;
    color: #ffffff;
  }

  .navIcon {
    margin: auto;
    width: 150px;
    height: auto;
  }

  .login_button {
    color: #ffffff !important;
  }

  .create_button {
    border-radius: 3px;
    color: #ffffff !important;
  }
}

@media screen and (min-width: 1200px) {
  .navigation {
    margin: auto;
    background-color: #cb202d;
    padding: 0px 20px;
    color: #ffffff;
  }

  .navIcon {
    margin: auto;
    width: 200px;
    height: auto;
  }

  .login_button {
    color: #ffffff !important;
  }

  .login_button:hover {
    border-radius: 3px;
    background-color: #d94148;
    cursor: pointer;
  }

  .create_button {
    border-radius: 3px;
    background-color: #ba1e2b;
    color: #ffffff !important;
  }

  .create_button:hover {
    background-color: #d94148;
  }
}

.login {
    margin: 100px auto;
    border-radius: 5px;
    background-color: #ffffff;
}

.registration_text {
    padding: 5px;
    font-size: 20px;
}

.login_field {
    padding: 25px;
}

.signin_button {
    background-color: #cb202d;
    border-color: #cb202d;
    padding: 10px 30px;
    font-size: 20px;
    width: 100%;
}

.signin_button:hover{
    border-color: #ba1e2b;
    background-color: #ba1e2b;
}

.terms_a{
    color: #cb202d !important;
}

.register {
  margin: 80px auto;
  border-radius: 5px;
  background-color: #ffffff;
}

.registration_text {
  padding: 5px;
  font-size: 20px;
}

.terms {
  font-size: 15px;
  color: #89959b;
}

.terms_a {
  color: #cb202d;
}

.register_button {
  background-color: #cb202d;
  border-color: #cb202d;
  padding: 10px 30px;
  font-size: 20px;
  width: 100%;
}

.register_button:hover{
    border-color: #ba1e2b;
    background-color: #ba1e2b;
}

.review_card {
  border-radius: 5px;
  background-color: #f5f5f5;
  margin: 5px 0px;
  padding: 10px 0px 10px 20px;
}

.review_image {
  object-fit: cover;
  padding: 10px 0px;
  width: 100%;
  height: 300px;
}

.review_data {
  position: relative;
  text-align: left;
  padding: 10px 0px;
}

.review_photo {
  margin: auto;
  padding: 10px 0px;
  border-radius: 50%;
  max-height: 100px;
  max-width: 100px;
}
.reviewer_name {
  margin: auto;
  font-size: 20px;
  padding: 10px 0px 0px 100px;
  text-align: left;
}

.reviewer_reviews {
  margin-bottom: 5px;
  font-size: 13px;
  color: #89959b;
  padding-left: 100px;
  text-align: left;
}

.rating {
  margin-top: 5px;
  float: left;
  font-size: 30px;
}

h2 {
  font-size: 22px;
}

.review_name {
  margin: auto;
  font-size: 40px;
  font-weight: bold;
  text-align: left;
  color: #cb202d;
}

.review_data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.review_city,
.review_type,
.review_open,
.review_phone,
.review_cost,
.review_time,
.review_views,
.review_reviews {
  font-size: 17px;
}

.review_rating {
  color: #ffffff;
  text-align: center;
  font-size: 25px;
  background-color: #3f7e00;
  padding: 10px 15px;
  border-radius: 5px;
}

@media screen and (max-width: 470px) {
  .review {
    margin: auto;
    text-align: center;
    border-radius: 5px;
    padding: 25px;
  }

  .review_data {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  h2 {
    font-size: 18px;
  }

  .review_name {
    font-size: 30px;
    font-weight: bold;
    text-align: left;
    color: #cb202d;
  }

  .review_city,
  .review_type,
  .review_open,
  .review_phone,
  .review_cost,
  .review_time,
  .review_views,
  .review_reviews {
    font-size: 15px;
  }

  .review_rating {
    color: #ffffff;
    text-align: center;
    font-size: 18px;
    background-color: #3f7e00;
    padding: 5px 0px;
    width: 100%;
    border-radius: 5px;
  }

  .reviewer_rate {
    float: left;
    margin-left: 20px;
    font-size: 15px;
  }

  .reviewer_rating {
    margin-left: 5px;
    float: left;
    background-color: green;
    color: #ffffff;
    font-size: 15px;
    padding: 0px 10px;
    border-radius: 5px;
  }

  .reviewer_review_data {
    margin-top: 15px;
    word-break: break-all;
    padding-right: 20px;
    font-size: 15px;
    text-align: left;
  }

  .review_button {
    margin-top: 10px;
    float: right;
  }

  .review_box {
    margin-top: 20px;
  }
}

@media screen and (min-width: 471px) {
  .review {
    margin: auto;
    text-align: center;
    border-radius: 5px;
    padding: 50px 0px;
  }

  .reviewer_review {
    margin: auto;
    font-size: 12px;
    text-align: left;
  }

  .reviewer_rate {
    margin-left: 20px;
    font-size: 15px;
  }

  .reviewer_rating {
    margin: auto;
    background-color: green;
    color: #ffffff;
    font-size: 15px;
    padding: 0px 10px;
    border-radius: 5px;
  }

  .reviewer_review_data {
    margin: auto;
    word-break: break-all;
    font-size: 15px;
    padding-right: 20px;
    text-align: left;
  }

  /* .review_add {
    position: relative;
  } */
  .review_box {
    margin-top: 20px;
  }

  .review_button {
    margin: 10px 0px;
    float: right;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.containerBody {
  margin: 30px auto;
  padding: 20px;
  border-radius: 5px;
  background-color: #ebebe0;
}

.content {
  margin-top: 20px;
  padding: 20px;
  border-radius: 5px;
  background-color: #ffffff;
}

.restaurant_text {
  padding: 5px;
  color: #89959b;
}

#restaurant_button {
  width: 100%;
}

/* For Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

/* Webkit browsers like Safari and Chrome */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.Error{
    margin: 50px;
    text-align: center;
    font-size: 50px;
}
img.bg {
  min-height: 100%;
  min-width: 1024px;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
}

.main {
  position: relative;
}

.text {
  position: absolute;
  font-family: "Dancing Script", cursive;
  top: 150px;
  left: 5%;
  font-size: 40px;
  color: #221c17;
}

.button {
  position: absolute;
  top: 300px;
  left: 5%;
}

@media only screen and (max-width: 470px) {
  img.bg {
    left: 50%;
    margin-left: -512px;
  }

  .text {
    position: absolute;
    font-family: "Dancing Script", cursive;
    top: 175px;
    left: 5%;
    font-size: 20px;
    color: #221c17;
  }

  .button {
    position: absolute;
    top: 250px;
    left: 5%;
  }
}

@media only screen and (min-width: 471px) and (max-width: 1024px) {
  img.bg {
    left: 50%;
    margin-left: -512px;
  }

  .text {
    position: absolute;
    font-family: "Dancing Script", cursive;
    top: 200px;
    left: 5%;
    font-size: 30px;
    color: #221c17;
  }

  .button {
    position: absolute;
    top: 300px;
    left: 5%;
  }
}

